import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import ListContent from '../ListContent';
import Consultations from '../Consultations';
import './OnlineConsultation.css';
import { withTranslation } from 'react-i18next';
import Collapse from '@material-ui/core/Collapse';

class OnlineConsultation extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 0,
      consultationsEnabled: false
    };
  }

  getData() {
    return {
      'online-consultation.nutritional-education.title': this.getDetails('online-consultation.nutritional-education', 3),
      'online-consultation.overweight.title': this.getDetails('online-consultation.overweight', 2),
      'online-consultation.clinic-nutrition.title': this.getDetails('online-consultation.clinic-nutrition', 4),
      'online-consultation.life-stages.title': this.getDetails('online-consultation.life-stages', 3),
      'online-consultation.vegetarian-diet.title': this.getDetails('online-consultation.vegetarian-diet', 2)
    }
  }

  getDetails(key, itemNumber) {
    const { t } = this.props;
    return( 
      <ul className='details-list'>
        { Array(itemNumber).fill().map((_, index) => 
            <li>{ t(key + '.content.item' + (index + 1)) }</li>
          ) 
        }
      </ul>
      );
  }

  render() {
    const { t } = this.props;
    return (
      <div className='service'>
        <div className='consultation-container'>
          <div className='title'>
            <p>{ t('online-consultation.title.part1') } { t('online-consultation.title.part2') }</p>
          </div>
          <ListContent data={ this.getData() } />
          <div className='actions'>
            <Button size='large' 
                    variant='contained'
                    tabIndex={59}
                    color={ this.state.consultationsEnabled ? 'secondary' : 'primary' } 
                    onClick={ () => {
                      this.setState({consultationsEnabled: !this.state.consultationsEnabled});
                    }
            }>
              { t('online-consultation.info-feeds') }
            </Button>
          </div>
        </div>
        <Collapse in={ this.state.consultationsEnabled }>
          <div>
            <Consultations />
          </div>
        </Collapse>
      </div>
    );
  }
}

export default withTranslation()(OnlineConsultation);
