import React, { Component } from 'react';
import logo from '../../assets/logo_no_name.png';
import './FixedNavbar.css';
import { withTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

class FixedNavbar extends Component {

    componentDidMount() {
        const navElement = document.querySelector('nav.fixed-nav-alt');
        const ulElement = navElement.querySelector('ul');
        ulElement.style.height = ulElement.offsetHeight + 'px';
        navElement.style.height = 'max-content';
    }

    render() {
        
        const { t } = this.props;

        return (
            <nav className='fixed-nav fixed-nav-alt'>
                <ul className='colored'>
                    <li className='main-button' tabIndex={1}>
                        <Link to='/' className='navbar-logo'>
                            <img src={logo} />
                        </Link>
                    </li>
                    <li className='contact-button' tabIndex={4}>
                        <Link to='/'>
                            { t('menu.back-main-menu') }
                        </Link>
                    </li>
                </ul>
            </nav>
        );
    }
}

export default withTranslation()(FixedNavbar);
  
  