import React, { Component } from 'react';
import './ListContent.css';
import { withTranslation } from 'react-i18next';

class ListContent extends Component {

  constructor(props) {
    super(props);
    const list = Object.keys(this.props.data);
    this.state = {
      list,
      selected: 0
    };

    this.content = Object.values(this.props.data);
  }

  addColorClass(previousClasses, index) {
    let result = previousClasses.slice(0);
    if (index % 2 === 0) {
      result.push('alt-primary');
    } else {
      result.push('alt-secondary');
    }
    return result;
  }

  getUpperList() {
    const { t, extraMargin } = this.props;
    let classes = extraMargin ? ['extra-margin'] : [];
    if (this.props.fixed === 'up') {
      return this.state.list.map(
        (item, index) => {
          return <li className={ this.addColorClass(classes, index).join(' ') }>
            { t(item) }
          </li>;
        }
      )
    } else if (!this.props.fixed) {
      const upperList = this.state.list.slice(0, this.state.selected + 1);
      return upperList.map(
        (item, index) => {
          return <li onClick={() => this.setState({selected: index})}
              className={ this.addColorClass(classes, index).join(' ') }>
            { t(item) }
          </li>
        }
      );
    }
  }

  getLowerList() {
    const { t, extraMargin } = this.props;
    let classes = extraMargin ? ['extra-margin'] : [];
    if (this.props.fixed === 'down') {
      return this.state.list.map(((item, index) => 
      <li className={ this.addColorClass(classes, index).join(' ') }>
        { t(item) }
      </li>));
    } else if (!this.props.fixed) {
      const lowerList = this.state.list.slice(this.state.selected + 1, this.state.list.length);
      return lowerList.map((item, index) => {
        const realIndex = this.state.selected + index + 1;
        return (
          <li onClick={() => this.setState({selected: realIndex})}
              className={ this.addColorClass(classes, realIndex).join(' ') }>
            { t(item) }
          </li>
        );
      });
    }
  }

  render() {
    return (
      <div id='listContentWrapper'>
        <div id='listContentContainer'>
          <div id='upperListFragment' className='list-fragment'>
            { this.getUpperList() }
          </div>
          <div id='contentFragment'>
            { this.content[this.state.selected] }
          </div>
          <div id='lowerListFragment' className='list-fragment'>
            { this.getLowerList() }
          </div>
        </div>
        <div id='listContentExpanded'>
          { this.content[this.state.selected] }
        </div>
      </div>
    );
  }
}

export default withTranslation()(ListContent);
