import React, { Component } from 'react';
import logo from '../../assets/logo_no_name.png';
import './Navbar.css';
import { withTranslation } from 'react-i18next';
import Container from '@material-ui/core/Container';

class Navbar extends Component {

    setScrollingListener(liElement, scrollToElement) {
        var li = document.querySelector(liElement);
        li.addEventListener('click', function(){
            var element = document.querySelector(scrollToElement);            
            window.scrollTo({ top: element.offsetTop - li.offsetHeight, 
                              behavior: 'smooth' }); 
        });
    }

    componentDidMount() {
        const navElement = document.querySelector('nav');
        this.setScrollingListener = this.setScrollingListener.bind(navElement);
        this.setScrollingListener('li .navbar-logo', 'div.Main');
        this.setScrollingListener('li.info-button', 'div.Info');
        this.setScrollingListener('li.about-button', 'div.About');
        this.setScrollingListener('li.contact-button', 'div.Contact');

        const logoElement = navElement.querySelector('.logo-big');
        const originalHeight = logoElement.offsetHeight;
        const ulElement = navElement.querySelector('ul');
        const logoLi = ulElement.querySelector('.main-button');
        logoElement.style.height = originalHeight + 'px';
        ulElement.style.height = ulElement.offsetHeight + 'px';
        navElement.style.height = 'max-content';
        document.addEventListener('scroll', function() {
            logoLi.classList.remove('hidden');
            ulElement.classList.remove('colored');
            const currentScrollY = window.scrollY;
            const newHeight = originalHeight - currentScrollY;
            if (newHeight > 0) {
                logoElement.style.height = newHeight + 'px';
                logoLi.classList.add('hidden');
            } else {
                logoElement.style.height = '0px';
                ulElement.classList.add('colored');
            }
        });
    }

    render() {
        
        const { t } = this.props;

        return (
            <nav className='fixed-nav'>
                <div className='logo-big-container'>
                    <Container maxWidth='xl' className='logo-big' />
                </div>
                <ul>
                    <li className='main-button hidden' tabIndex={1}>
                        <img className='navbar-logo' src={logo} />
                    </li>
                    <li className='info-button' tabIndex={2}>
                        { t('menu.services') }
                    </li>
                    <li className='about-button' tabIndex={3}>
                        { t('menu.about-me') }
                    </li>
                    <li className='contact-button' tabIndex={4}>
                        { t('menu.contact') }
                    </li>
                </ul>
            </nav>
        );
    }
}

export default withTranslation()(Navbar);
  
  