import React, { Component } from 'react';
import OnlineConsultation from './OnlineConsultation';
import Workshops from './Workshops';
import Colaborations from './Colaborations';
import ServiceIcon from './ServiceIcon';
import online_consultation_img from '../../../assets/personalized_consultation1.png';
import workshops_img from '../../../assets/workshops1.png';
import colaborations_img from '../../../assets/colaborations.png';
import './Info.css';
import { withTranslation } from 'react-i18next';
import Fade from '@material-ui/core/Fade';

class Info extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selected: 'online-consultation',
      minHeight: null
    };
  }

  render() {
    const { t } = this.props;
    return (
      <div className='Info' id='info'>
        <div className='services'>
          <div id='personalized-consultation'
               className={ this.state.selected === 'online-consultation' ? 'selected' : '' }>
            <ServiceIcon title={ t('info.online-consultation') }
                         image={ online_consultation_img }
                         tabIndex={6}
                         onClick={() => (this.setState({ selected: 'online-consultation' }))}/>
            <OnlineConsultation />
          </div>
          <div id='workshops'
               className={ this.state.selected === 'workshops' ? 'selected' : '' }>
            <ServiceIcon title={ t('info.workshops') }
                         image={ workshops_img }
                         tabIndex={7}
                         onClick={() => (this.setState({ selected: 'workshops' }))}/>
            <Workshops />
          </div>
          <div id='colaborations'
               className={ this.state.selected === 'colaborations' ? 'selected' : '' }>
            <ServiceIcon title={ t('info.colaborations') } 
                         image={ colaborations_img }
                         tabIndex={8}
                         onClick={() => (this.setState({ selected: 'colaborations' }))}/>
            <Colaborations />
          </div>
        </div>
        <div className='services-expanded' 
             style={ this.state.minHeight ? { minHeight: this.setState.minHeight + 'px'} : {} }>
          <Fade in={ this.state.selected === 'online-consultation' } timeout={ {enter:500} } unmountOnExit mountOnEnter>
            <div>
              <OnlineConsultation />
            </div>
          </Fade>
          <Fade in={ this.state.selected === 'workshops' } timeout={ {enter:500} } unmountOnExit mountOnEnter>
            <div>
              <Workshops />
            </div>
          </Fade>
          <Fade in={ this.state.selected === 'colaborations' } timeout={ {enter:500} } unmountOnExit mountOnEnter>
            <div>
              <Colaborations />
            </div>
          </Fade>
        </div>
      </div>
    );
  }
}

export default withTranslation()(Info);
