import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import './ServiceForm.css';
import { withTranslation } from 'react-i18next';

class ServiceForm extends Component {

  render() {
    const { t } = this.props;

    return (
      <div className='form-container'>
        <div className='form-title label primary'>{ this.props.title }</div>
        <div className='service-form'>
          <FormControl fullWidth>
            <TextField required label={ t('form.name') } />
          </FormControl>
          <FormControl fullWidth>
            <TextField  required label={ t('form.email') }/>
          </FormControl>
          <FormControl fullWidth>
            <TextField  required multiline rows={10} label={ t('form.hint') } />
          </FormControl>
          <div className='form-actions'>
            <Button size='large' variant='contained' color='secondary'>{ t('form.send') }</Button>
          </div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(ServiceForm);
