import React, { Component } from 'react';
import ListContent from '../ListContent';
import './Workshops.css';
import { withTranslation } from 'react-i18next';

class Workshops extends Component {

  getData() {
    const data = {
      'workshops.trainings': this.getDetail(),
      'workshops.workshops': this.getDetail(),
      'workshops.conferences': this.getDetail()
    };
    return data;
  }

  getDetail() {
    const { t } = this.props;

    return (
      <>
        <p>{ t('workshops.detail.part1') }</p>
        <ul>
          <li>{ t('workshops.detail.part2') }</li>
          <li>{ t('workshops.detail.part3') }</li>
          <li>{ t('workshops.detail.part4') }</li>
        </ul>
        <p>{ t('workshops.detail.part5') }</p>
      </>
    );
  }

  render() {
    const { t } = this.props;

    return (
      <div className='workshop-container service'>
        <div className='title'>
          <p>{ t('workshops.title.part1') } { t('workshops.title.part2') }</p>
        </div>
        <ListContent data={ this.getData() } fixed='up' extraMargin={ true } />
      </div>
    );
  }
}

export default withTranslation()(Workshops);
