import React, { Component } from 'react';
import nutriendo_empresa from '../../../../assets/nutriendo_empresa.jpeg';
import ListContent from '../ListContent';
import './Colaborations.css';
import { withTranslation } from 'react-i18next';

class Colaborations extends Component {

  getDetails() {
    const { t } = this.props;

    return (
      <>
        <p>{ t('colaborations.detail') }</p>
        <div className='actions'>
          <a href='https://www.instagram.com/nutriendoempresa/'>
            <img src={ nutriendo_empresa } />
          </a>
        </div>
      </>
    );
  }

  getData() {
    const data = {
      'colaborations.valuation': this.getDetails(),
      'colaborations.assessment': this.getDetails()
    }
    return data;
  }

  render() {
    const { t } = this.props;

    return (
      <div className='colaborations-container service'>
        <div className='title'>
          <p>{ t('colaborations.title.part1') } { t('colaborations.title.part2') }</p>
        </div>
        <ListContent data={ this.getData() } fixed='up' extraMargin={ true } />
      </div>
    );
  }
}

export default withTranslation()(Colaborations);
